export default {
	data() {
		return {
			course: '点击选择',
			courselist: ['素描', '油画', '插画'],

			shop: '点击选择',
			shoplist: ['白云店', '黄沙店']
		};
	},
	methods: {
		_select(e) {
			console.log(e.detail.value);
			let {
				courselist
			} = this;
			this.course = courselist[+e.detail.value]
		},
		shop_sele(e) {
			console.log(e.detail.value);
			let {
				shoplist
			} = this;
			this.shop = shoplist[+e.detail.value]
		}
	}
}
